export function randomIntegerUpTo(max) {
    const between0and1 = Math.random();
    const floatBetween0andMax = between0and1 * max;
    return Math.floor(floatBetween0andMax);
}
export function range(n) {
    let retVal = [];
    for (let i = 0; i < n; i++) {
        retVal.push(i);
    }
    return retVal;
}
export function replaceAtIndex(array, index, value) {
    let retVal = new Array();
    for (let i = 0; i < array.length; i++) {
        retVal[i] = (i === index) ? value : array[i];
    }
    return retVal;
}
export function outerJoin(v1, v2, defaultV1, defaultV2) {
    const allKeys = [...new Set([
            ...v1.keys(),
            ...v2.keys()
        ]).values()];
    let map = new Map();
    for (let key of allKeys) {
        map.set(key, [v1.get(key) || defaultV1, v2.get(key) || defaultV2]);
    }
    return map;
}
export function innerJoin(v1, v2) {
    const commonKeys = [...new Set([
            ...v1.keys(),
            ...v2.keys()
        ]).values()].filter(k => v1.has(k) && v2.has(k));
    let map = new Map();
    for (let key of commonKeys) {
        map.set(key, [v1.get(key), v2.get(key)]);
    }
    return map;
}
