export function Multiset(array) {
    switch (array.length) {
        case 0:
            return empty();
        default:
            const [first, ...rest] = array;
            return sum(singleton(first), Multiset(rest));
    }
}
export function singleton(element, amount = 1) {
    return { elements: new Map([[element, amount]]) };
}
export function empty() {
    return { elements: new Map() };
}
export function isSubset(a, b) {
    return universe(a, b).every(x => count(a, x) <= count(b, x));
}
export function intersection(a, b) {
    return {
        elements: new Map(universe(a, b).map(x => [x, Math.min(count(a, x), count(b, x))]))
    };
}
export function union(a, b) {
    return {
        elements: new Map(universe(a, b).map(x => [x, Math.max(count(a, x), count(b, x))]))
    };
}
export function sum(a, b) {
    return {
        elements: new Map(universe(a, b).map(x => [x, count(a, x) + count(b, x)]))
    };
}
export function sumAll(array) {
    switch (array.length) {
        case 0:
            return empty();
        default:
            const [first, ...rest] = array;
            return sum(first, sumAll(rest));
    }
}
export function difference(a, b) {
    const UnivrseA = universe(a, b).map(x => [x, count(a, x) - count(b, x)]);
    return {
        elements: new Map(UnivrseA.filter(([a, b]) => b > 0))
    };
}
export function multiplyCounts(n, multiset) {
    return {
        elements: new Map([...multiset.elements.entries()]
            .map(([k, v]) => [k, v * n]))
    };
}
export function count(multiset, element) {
    return multiset.elements.has(element) ? multiset.elements.get(element) : 0;
}
export function isEmpty(multiset) {
    return multiset.elements.size === 0;
}
function universe(a, b) {
    return [...new Set([
            ...a.elements.keys(),
            ...b.elements.keys()
        ]).values()];
}
export function equals(a, b) {
    return universe(a, b).every(element => count(a, element) === count(b, element));
    // test cases
    // Ignoring efficiency, what is true if the following are true?
    // Scenario A: Multiset.isEmpty(Multiset.difference(a, b))
    // Scenario B: Multiset.isEmpty(Multiset.difference(b, a))
    // Scenario C: Multiset.isSubset(a, b) && Multiset.isSubset(b, a)
    // Scenario D: Multiset.isEmpty(a) && Multiset.isEmpty(b)
}
export function toStringMultiset(multiset) {
    return `{ ${[...multiset.elements.entries()].map(([k, v]) => `${k}: ${v}`).join(', ')} }`;
}
